*{
    box-sizing: border-box;
}
.App{
    color: #776e65;
    width: 100vw;
    height: 100vh;
    display: grid;
    padding: 1rem;
    background-color:whitesmoke;
    overflow: hidden;
}
.Game{
    display: flex;
    justify-content: center;
    align-items: center;
}
.Grid-container{
    width: 100%;
    max-width: 400px;
    max-height: 400px;
    aspect-ratio: 1;
    position: relative;
    border: 5px inset cornflowerblue;
}
.grid{ 
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    gap : .5em;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: repeat(4,1fr);
    padding: .5em;
}
.Grid-bg{
    position: absolute;
    /* z-index: -1; */
}
.Grid-bg-cell{
    background-color: rgba(37, 95, 203, 0.301);
}

@media only screen and (max-width: 600px) {
   
  }

  .Tile{
    border: 5px outset cornflowerblue;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
    font-size: xx-large;
    background-color: hsl(219, 100%, 80%);
    color: white;
    opacity: 0;
    z-index: 10;
    position: relative;
    grid-row: 1;
    grid-column: 1;
    transition: all .1s;
    animation: fadein .1s ease-in-out .1s 1 normal forwards;
}
.tile-4{
    background-color: hsl(219, 100%, 75%);
}
.tile-8{
    background-color: hsl(219, 100%, 70%);
}
.tile-16{
    background-color: hsl(219, 100%, 65%);
}
.tile-32{
    background-color: hsl(219, 100%, 60%);
}
.tile-64{
    background-color: hsl(219, 100%, 50%);
}
.tile-128{
    background-color: hsl(219, 100%, 45%);
}
.tile-256{
    background-color: hsl(219, 100%, 35%);
}
.tile-512{
    background-color: hsl(219, 100%, 25%);
}
.tile-1024{
    background-color: hsl(219, 100%, 15%);
}
.tile-2048{
    background-color: hsl(219, 100%, 10%);
}
.merged{
    /* animation: name duration timing-function delay iteration-count direction fill-mode; */
    /* animation: merged 1s ease-in-out 1s 1 normal forwards; */
}

@keyframes fadein {
    from{
        transform: scale(.8);
        opacity: 0;
    }
    to{
        transform: scale(1);
        opacity: 1;
    }
}
@keyframes merged {
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}